@import url(/markdown-github.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{
  width: 80%;
  margin: 0 auto;
}


.header{
  background-color:#5a5555;
  height:50px;
}

.header img{
  max-width: 120px;
  margin-left: 50px;
  margin-top: 10px;
}

.header ul{
  list-style: none; /* ul的小点点样式，none是没有 */
  float: right; /*漂浮在图片上，不在普通流了*/
  margin: 4px; /*改小外边距，防止下移 */
}

.header li{
  display: inline-block; /* 让li有行内属性，从而 水平*/
  padding: 10px 15px;
}

.header a{
  text-decoration: none;
  color: #ccc;
  font-size: 14px;
  text-shadow: none;
}
.PostList{
  background-color: #e1e1e1;
}
.PostList .posts {
  margin-top: 10px;
}

.PostList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}

.PostList ul {
  list-style: none;
  width: 100%;
  max-width: 1344px;
  margin: 0 auto;
}

.PostList ul li:not(:first-child) { /*子元素中，除外第一个 */
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma, "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  color: #333;
  border-top: 1px solid #f0f0f0;
}

.PostList li:not(:first-child):hover {
  background: #f5f5f5;;
}

.PostList li:last-child:hover {
  background: white;
}

li span {
  line-height: 30px;
}

.PostList .allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
}

.PostList .reply_count {
  color: #9e78c0;
  font-size: 14px;
}

.PostList .put_good, .put_top { /* 帖子分类，good-精华，top-置顶 */ 
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 12px;
  margin-right: 10px;
}

.PostList .topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  font-size: 12px;
  margin-right: 10px;
}

.PostList .last_reply {
  text-align: right;
  min-width: 50px;
  display: inline-block;
  white-space: nowrap;
  float: right;
  color: #778087;
  font-size: 12px;
}

.PostList .toobar {/* 第二顶部栏 “全部 精华 分享 问答 招聘” */
  height: 40px;
  background-color: #f5f5f5;
}

.PostList .toobar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
}

.PostList .toobar span:hover {
  color: #9e78c0;
}

.PostList a {
  text-decoration: none;
  color: black;
}

.PostList a:hover {
  text-decoration: underline;
}

.PostList .loading {
  text-align: center;
  padding-top: 300px;
}

.pagination {
  margin-top: 5px;
  margin-bottom: 20px;
  background-color: white;
  padding: 6px 20px;
  border-radius: 5px;
  /*box-shadow: 0px 2px 9px #888888;*/
  border: 1px solid #888888;
}

.pagination button {
  background-color: #fff;
  border: 1px solid #ddd;
  color: #778087;
  border-radius: 3px;
  outline: none;
  height: 21px;
  cursor: pointer;
  padding: 0 2px;
  width: 55px;
  height: 29px;
}

.pagination .pagebtn {
  position: relative;
  bottom: 1px;
  width: 40px;
  margin: 0 4px;
}

.pagination .currentPage {
  color: white;
  background-color: #1f1b1b;

}

/*引入处理markdown的css*/

.topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 16px;
  font-size: 12px;
  margin-top: 10px;
}

.article:not(:first-child) {
  margin-right: 340px;
  margin-top: 15px;
}

#reply,
.topic_header {
  background-color: #fff;
}

#reply {
  margin-top: 15px;
}

.replyUp img {
  width: 30px;
  height: 30px;
  position: relative;
  bottom: -9px;
}

#reply a,
#reply span {
  font-size: 13px;
  color: #666;
  text-decoration: none;
}
.replySec {
  border-bottom: 1px solid #e5e5e5;
  padding: 0 10px;
}

.loading {
  text-align: center;
  padding-top: 300px;
}

.replyUp a:nth-of-type(2) {
  margin-left: 0px;
  display: inline-block;
}

.topic_header {
  padding: 10px;
}

.topic_title {
  font-size: 20px;
  font-weight: bold;
  padding-top: 8px;
}

.topic_header ul {
  list-style: none;
  padding: 0px 0px;
  margin: 6px 0px;
}

.topic_header li {
  display: inline-block;
  font-size: 12px;
  color: #838383;
}

.topic_content {
  border-top: 1px solid #e5e5e5;
  padding: 0 10px;
}

.markdown-text img {
  width: 92% !important;
}

.autherinfo .authersummay, .autherinfo .recent_replies, .autherinfo .recent_topics {
  background-color: #fff;
}
.autherinfo {
  width: 328px;
  float: right;
  margin-top: 0;
}
.autherinfo li{
  padding: 3px 0 ;
}
.autherinfo .recent_replies ul, .autherinfo .recent_topics ul {
  margin-top: 0px;
  margin-bottom: 0px;
  list-style: none;
  padding-left: 14px;
}

.autherinfo ul a {
  font-size: 12px;
  text-decoration: none;
  color: #778087;
}

.autherinfo .topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 16px;
  font-size: 12px;
  margin-top: 10px;
}

.autherinfo img {
  height: 48px;
  width: 48px;
  border-radius: 3px;
  margin: 10px;
}

.autherinfo .loginname {
  width: 100px;
  float: right;
  margin-top: 22px;
  margin-right: 159px;
  font-size: 14px;
}

.autherinfo .loginname a {
  text-decoration: none;
  color: #778087;
}

.autherinfo .authersummary .topbar {
  margin-top: 0px;
}


.userInfomation {
  background: white;
  width: 75%;
  margin: 10px auto;
}
.userInfomation section {
  padding: 12px;
}
.userInfomation img {
  width: 30px;
}
.userInfomation li {
  list-style: none;
}
.userInfomation .replies,
.userInfomation .topics {
  font-size: 0.72rem;
  border-top: 10px #dddddd solid;
}
.userInfomation > div > p {
  padding: 12px 0 12px 12px;
  background-color: rgba(212, 205, 205, 0.17);
  font-size: 0.75rem;
  margin: 0;
}
.userInfomation > div > ul > li {
  padding: 4px 0 4px 12px;
  white-space: nowrap;
  font-size: 0.72rem;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 30px;
  vertical-align: middle;
}
.userInfomation > div > ul > li > a {
  color: #094e99;
  text-decoration: none;
}
